import React, { Component } from 'react';
import { Route, Link } from 'react-router-dom';
import './App.css';
import ShoppingSiteSlow from './ShoppingSiteSlow';
import ConsoleErrorPage from './ConsoleErrorPage';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Link to="/slow-shopping-site">Slow Shopping Site</Link>
        <hr />
        <Route exact path="/slow-shopping-site" component={ShoppingSiteSlow} />
        <Route exact path="/console" component={ConsoleErrorPage} />
      </div>
    );
  }
}

export default App;



